.conditions-section {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  max-width: 1228px;
  min-height: 710px;
  margin-right: auto;
  background-image: url(../img/background-desktop-conditions.png);
  background-position: left;
  background-repeat: no-repeat; }

.conditions-section__container {
  width: 50%;
  padding-left: 90px; }

.conditions-section__heading {
  margin: 0;
  padding: 0;
  font-size: 40px;
  line-height: 130%;
  text-transform: uppercase;
  font-weight: bold;
  color: #1fc0fc;
  margin-bottom: 24px;
  text-align: left; }

.conditions-section__description {
  position: relative;
  max-width: 480px;
  margin-bottom: 32px; }
  .conditions-section__description--aftermath {
    max-width: 536px;
    padding-left: 15px;
    opacity: 0.5; }
    .conditions-section__description--aftermath::after {
      content: "";
      position: absolute;
      left: 0;
      top: 0;
      display: block;
      height: 100%;
      width: 5px;
      background: linear-gradient(to right, #ffc341, #ffd701); }

.conditions-section__condition {
  display: block;
  max-width: 480px;
  margin-bottom: 30px;
  font-size: 17px;
  font-weight: bold; }

.conditions-section__list {
  margin: 0;
  padding: 0;
  list-style: none; }

.conditions-section__item-description {
  margin: 0;
  padding: 0; }

.conditions-section__item {
  position: relative;
  max-width: 480px;
  padding-left: 60px;
  margin-bottom: 48px; }
  .conditions-section__item::after {
    content: "";
    position: absolute;
    left: 0;
    top: 50%;
    display: block;
    width: 36px;
    height: 36px;
    background-color: #ffffff;
    box-shadow: 0px 3px 12px rgba(0, 0, 0, 0.1);
    border-radius: 50%;
    transform: translateY(-50%); }
  .conditions-section__item::before {
    content: "";
    position: absolute;
    left: 14px;
    top: 50%;
    font-family: "Fira Sans", "Arial", sans-serif;
    font-size: 15px;
    line-height: 150%;
    font-weight: bold;
    color: #ffc341;
    display: block;
    transform: translateY(-50%);
    z-index: 1; }
  .conditions-section__item--age::before {
    content: "1"; }
  .conditions-section__item--education::before {
    content: "2"; }
  .conditions-section__item--birth::before {
    content: "3"; }
  .conditions-section__item--residence::before {
    content: "4"; }

.gallery-section__heading {
  margin: 0;
  padding: 0;
  font-size: 40px;
  line-height: 130%;
  text-transform: uppercase;
  font-weight: bold;
  color: #1fc0fc;
  margin-bottom: 10px;
  text-align: center; }

.gallery-section__description {
  width: 720px;
  margin: 0 auto;
  text-align: center;
  font-size: 17px; }

.gallery-section__list {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: flex-end;
  margin: 0;
  padding: 0;
  list-style: none; }

.gallery-section__item {
  position: relative; }
  .gallery-section__item:nth-child(1) {
    width: 482px;
    margin-right: 15px; }
  .gallery-section__item:nth-child(2) {
    width: 640px; }

.gallery-section__item-description {
  position: absolute;
  bottom: 32px;
  left: 40px;
  width: 120px;
  font-family: 'Raleway', 'Arial';
  font-size: 21px;
  font-weight: 800;
  color: #ffffff; }
  .gallery-section__item-description--big {
    width: 320px; }

.gallery-section__minot-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin: 0;
  padding: 0;
  list-style: none; }

.gallery-section__minor-item {
  position: relative; }
  .gallery-section__minor-item--language, .gallery-section__minor-item--education {
    margin-bottom: 20px; }

.exposition-section__container {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  width: 400px;
  margin-left: auto;
  margin-right: 10%; }

.exposition-section__heading {
  margin: 0;
  padding: 0;
  font-size: 40px;
  line-height: 130%;
  text-transform: uppercase;
  font-weight: bold;
  color: #1fc0fc;
  margin-bottom: 24px; }

.exposition-section__description {
  width: 390px;
  margin: 0;
  margin-bottom: 48px;
  padding: 0;
  font-size: 17px;
  line-height: 170%;
  color: #ffffff; }

.exposition-section__button {
  height: 60px;
  margin: 0;
  padding: 0;
  font-family: "Fira Sans", "Arial";
  font-size: 17px;
  text-align: center;
  text-transform: uppercase;
  font-weight: 500;
  color: #ffffff;
  border-radius: 30px;
  border: none;
  outline: none;
  cursor: pointer;
  background: linear-gradient(to right, #ffc341, #ffd701);
  width: 230px; }
  .exposition-section__button:hover, .exposition-section__button:focus {
    background: #ffc341; }
  .exposition-section__button:active {
    background: #ffd701; }

.questions-section {
  min-height: 803px;
  background-image: url(../img/desktop-questions.png);
  background-position: 100% 0;
  background-size: 40%;
  background-repeat: no-repeat; }

.questions-section__heading {
  margin: 0;
  padding: 0;
  font-size: 40px;
  line-height: 130%;
  text-transform: uppercase;
  font-weight: bold;
  color: #1fc0fc;
  padding-left: 110px; }

.questions-section__list {
  margin: 0;
  padding: 0;
  padding-left: 110px;
  counter-reset: myCounter; }

.questions-section__item {
  position: relative;
  width: 45%;
  padding: 32px 0;
  font-size: 17px;
  border-bottom: 1px solid rgba(72, 72, 72, 0.2);
  list-style: none; }
  .questions-section__item::before {
    position: absolute;
    left: 0;
    top: 28px;
    counter-increment: myCounter;
    content: counter(myCounter);
    font-size: 28px;
    font-weight: bold;
    color: #ffc341; }

.questions-section__item-staging {
  width: 70%;
  margin: 0;
  padding-left: 30px; }

.questions-section__item-button {
  position: absolute;
  top: 32px;
  right: 5%;
  display: inline-block;
  padding: 5px;
  background-color: inherit;
  border: solid #1fc0fc;
  border-width: 0 3px 3px 0;
  cursor: pointer;
  outline: none;
  transform: rotate(45deg);
  transition-duration: 0.25s; }
  .questions-section__item-button:hover, .questions-section__item-button:focus {
    box-shadow: 2px 2px 2px #ffc341; }

.questions-section__item-description {
  display: none;
  margin: 0;
  margin-top: 24px;
  opacity: 0.72; }
